/*=========================================================================================
  File Name: router.js
  Description: Routes for vue-router. Lazy loading is enabled.
  Object Strucutre:
                    path => router path
                    name => router name
                    component(lazy loading) => component to load
                    meta : {
                      rule => which user can have access (ACL)
                      breadcrumb => Add breadcrumb to specific page
                      pageTitle => Display title besides breadcrumb
                    }
  ----------------------------------------------------------------------------------------
  Item Name: Vuesax Admin - VueJS Dashboard Admin Template
  Author: Pixinvent
  Author URL: http://www.themeforest.net/user/pixinvent
==========================================================================================*/


import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    scrollBehavior() {
        return {x: 0, y: 0}
    },
    routes: [

        {
            // =============================================================================
            // MAIN LAYOUT ROUTES
            // =============================================================================
            path: '',
            component: () => import('./layouts/main/Main.vue'),
            children: [
                // =============================================================================
                // Theme Routes
                // =============================================================================
                {
                    path: '/',
                    name: 'home',
                    component: () => import('./views/Home.vue'),
                },
                {
                    path: '/monitor',
                    name: 'Monitor',
                    component: () => import('./views/Monitor.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Отладка', active: true },
                        ],
                        pageTitle: 'Отладка'
                    },
                },
                {
                    path: '/monitor/http',
                    name: 'MonitorHttp',
                    component: () => import('./views/monitor/Http.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Home', url: '/' },
                            { title: 'Отладка', url: '/monitor'},
                            { title: 'Монитор HTTP', active: true },
                        ],
                        pageTitle: 'Монитор HTTP'
                    },
                },
                {
                    path: '/monitor/all',
                    name: 'MonitorAll',
                    component: () => import('./views/monitor/All.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Home', url: '/' },
                            { title: 'Отладка', url: '/monitor'},
                            { title: 'Монитор Маяков', active: true },
                        ],
                        pageTitle: 'Монитор Маяков'
                    },
                },
                {
                    path: '/modules',
                    name: 'Modules',
                    component: () => import('./views/Modules.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Home', url: '/' },
                            { title: 'Модули', active: true },
                        ],
                        pageTitle: 'Модули'
                    }
                },
                {
                    path: '/modules/:section',
                    name: 'ModulesSection',
                    component: () => import('./views/modules/Section.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Home', url: '/' },
                            { title: 'Модули', url: '/modules'},
                            { title: 'Модули', active: true },
                        ],
                        pageTitle: 'Модули'
                    }
                },
                {
                    path: '/documents',
                    name: 'documents',
                    component: () => import('./views/Documents.vue'),
                },
                {
                    path: '/settings',
                    name: 'Settings',
                    component: () => import('./views/Settings.vue'),
                    meta: {
                        breadcrumb: [
                            { title: 'Home', url: '/' },
                            { title: 'Настройки', active: true },
                        ],
                        pageTitle: 'Настройки'
                    }
                },
            ],
        },
        // =============================================================================
        // FULL PAGE LAYOUTS
        // =============================================================================
        {
            path: '',
            component: () => import('@/layouts/full-page/FullPage.vue'),
            children: [
                // =============================================================================
                // PAGES
                // =============================================================================
                {
                    path: '/pages/login',
                    name: 'pageLogin',
                    component: () => import('@/views/pages/Login.vue')
                },
                {
                    path: '/pages/error-404',
                    name: 'pageError404',
                    component: () => import('@/views/pages/Error404.vue')
                },
            ]
        },
        // Redirect to 404 page, if no match found
        {
            path: '*',
            redirect: '/pages/error-404'
        }
    ],
})

router.afterEach(() => {
    // Remove initial loading
    const appLoading = document.getElementById('loading-bg')
    if (appLoading) {
        appLoading.style.display = "none";
    }
})

export default router
