export default {
    namespaced: true,
    state: {
        sections: []
    },
    mutations: {
        syncModules(state, data){
            state.sections = data;
        },
        addSections(state, data){
            state.sections.push(data)
        },
        addModule(state, data) {
            let index = state.sections.findIndex((i) => i.id === data.pid);

            if (index >= 0) {
                state.sections[index].modules.push(data);
            }
        },
        updateSections(state, data){
            let index = state.sections.findIndex((i) => i.id === data.id);
            state.sections[index].title = data.title;
        }
    },
    actions: {
        getSectionById({state}, id){
            let section = state.sections.find((i) => i.id === id);

            if(section){
                return Promise.resolve(section)
            }else{
                return Promise.reject()
            }
        },
        addModule({state}, data){
            let index = state.sections.findIndex((i) => i.id === data.sectionId);

            if(index >= 0){

                let newModule = {
                    id: data.id,
                    alias: data.alias
                };

                state.sections[index].modules.push(newModule);

                return Promise.resolve(newModule)
            }else{
                return Promise.reject()
            }
        }
    }
}