import Vue from 'vue'
import App from './App.vue'

//Test

// Vuesax Component Framework
import Vuesax from 'vuesax'
import 'material-icons/iconfont/material-icons.css' //Material Icons
import 'vuesax/dist/vuesax.css'; // Vuesax
Vue.use(Vuesax);

// Theme Configurations
import '../themeConfig.js'


// Globally Registered Components
import './globalComponents.js'


// Styles: SCSS
import './assets/scss/main.scss'


// Tailwind
import '@/assets/css/main.css';


// Vue Router
import router from './router'


// Vuex Store
import store from './store/store'


// PrismJS
import 'prismjs'
import 'prismjs/themes/prism-tomorrow.css'


// Feather font icon
require('./assets/css/iconfont.css')


Vue.config.productionTip = false;

import axios from 'axios';

const http = axios.create({
    baseURL: 'http://localhost:3000/api/v1/',
});

http.get('/sections').then((res) => {
    store.commit('modules/syncModules', res.data);
});

Vue.prototype.$http = http;
Vue.prototype.$shortId = require('shortid');

import io from 'socket.io-client';

let socket = io('http://localhost:3000');

socket.on('connect', () => {
    console.log('Socket connected!!!')
});

socket.on('log:http', (data) => {
    store.commit('http_log/addItemLog', data);
});

socket.on('models:sections:create', (data) => {
    store.commit('modules/addSections', data);
});

socket.on('models:sections:update', (data) => {
    store.commit('modules/updateSections', data);
});

socket.on('models:sections:delete', (data) => {
    console.log(data);
    // store.commit('modules/updateSections', data);
});

socket.on('models:module:create', (data) => {
    console.log('Create module', data);
    store.commit('modules/addModule', data);
});

socket.on('disconnect', function(){});



new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app');
